  @Input() campaign: Campaign;

  @Output() add: EventEmitter<AdRun> = new EventEmitter();

  @Output() edit: EventEmitter<AdRun> = new EventEmitter();

  @Output() deleteAdRun: EventEmitter<AdRun> = new EventEmitter();

  @Output() adRunsChange: EventEmitter<AdRun[]> = new EventEmitter();

  @Output() orderChanged: EventEmitter<AdRun[]> = new EventEmitter();

