  filteredSites: Array<any>;
  placements: Array<any>;
  filteredPlacements: Array<any>;

  formats: Array<string>;
  sizes: Array<any>;
  filteredSizes: IMultiSelectOption[];
  selectedSizes: string[];
  customSizes: string;

  private unsubscribe: Subject<any> = new Subject();

  readonly THOUSANDS_DELIMITED = createNumberMask({
    prefix: '',
    thousandsSeparatorSymbol: THOUSANDS_SEPARATOR_SYMBOL,
    allowDecimal: true,
    decimalLimit: DECIMAL_LIMIT
  });
  readonly MASKED_FIELDS = {
    thousandsDelimited: [
      'ratecard',
      'units',
      'estimatedAV',
      'estimatedCT',
      'estimatedReachUU',
      'estimatedCTR',
      'estimatedLead',
      'estimatedAcquisition'
    ]
  };

  readonly PRICING_TYPE_HUF_CT = 'HUF/CT';
  readonly PRICING_TYPE_HUF_AV = 'HUF/AV';

  readonly AD_RUN_BAG = 'ad-run-bag';

  readonly NORMAL = 1;
  readonly ADD = 2;
  readonly EDIT = 3;

  formState = this.NORMAL;
  formData: AdRun;

  calculatedValuesPerAdRuns = {};
  weeks = [];
  dateOfDays = [];
  monthSpans = [];

  isEditingMode = false;

  ngOnInit(): void {
    moment.locale('hu');

    this.route.paramMap
      .pipe(takeUntil(this.unsubscribe))
      .subscribe((params: ParamMap) => {
        this.isEditingMode = !!params.get('isEditingMode');
      });

    this.weeks = this.helperService.calculateWeeksForHeader(this.campaign);
    this.dateOfDays = this.helperService.calculateDatesForHeader(this.campaign);
    this.monthSpans = this.helperService.calculateMonthsForHeader(this.campaign);

    this.dragulaService.setOptions(this.AD_RUN_BAG, {
      mirrorContainer: this.mirrorContainer.nativeElement
    });

    this.selectedSizes = [];

    window.scrollTo(0, 0);
  }

  ngOnChanges(changes: SimpleChanges) {
    this.copyEntriesForDragula(changes);
    this.calculateFields();
  }

  ngOnDestroy() {
    if (this.dragulaService.find(this.AD_RUN_BAG)) {
      this.dragulaService.destroy(this.AD_RUN_BAG);
    }

    this.unsubscribe.next();
    this.unsubscribe.complete();
  }

  copyEntriesForDragula(changes) {
    const entries = changes.entries;
    const isEntriesChanged = entries && !(entries.previousValue === entries.currentValue);
    if (entries && (entries.firstChange || isEntriesChanged)) {
      this.entriesForDragula = [...this.entries];
    }
  }

  calculateFields() {
    this.calculatedValuesPerAdRuns = _.chain(this.entriesPlusForm)
      .keyBy('id')
      .mapValues(this.createCalculatedFieldsObject.bind(this))
      .value();
  }

  createCalculatedFieldsObject(adrun: AdRun) {
    return {
      sumRatecard: calculateSumRatecard(adrun),
      net: calculateNet(adrun),
      netNet: calculateNetNet(adrun),
      clientNet: calculateClientNet(adrun, this.campaign),
      cpm: calculateCPM(adrun),
      cpc: calculateCPC(adrun),
      cpt: calculateCPT(adrun),
      cpl: calculateCPL(adrun),
      cpa: calculateCPA(adrun),
      remainingUnits: calculateRemainingUnits(adrun)
    };
  }

